.container {
    width: 80%;
    margin: auto;
    padding: 20px;
    text-align: center;
  }
  
  .answers-container {
    
    flex-wrap: wrap;
    justify-content: center;
    gap: 10px;
    margin: auto;
    width: 50%;
  }
  
  .answer {
    
    background-color: lightgray;
    list-style-type: none;
    padding: 10px;
    cursor: pointer;
    margin: auto;
    transition: background-color 0.3s ease;
  }
  
  .answer:hover {
    background-color: darkgray;
  }
  
  .correct-answer, .incorrect-answer {
    width: 30%;
    margin: 10px auto;
    padding: 10px;
    border-radius: 20px;
    transition: background-color 0.3s ease;
  }
  
  .correct-answer {
    background-color: green;
    color: white;
  }
  
  .incorrect-answer {
    background-color: red;
    color: white;
  }
  
  .reset-button {
    padding: 10px 20px;
    margin-top: 20px;
    cursor: pointer;
  }
  
  @media (max-width: 768px) {
    .answer {
      width: 90%;
    }
  }
  
  @media (max-width: 480px) {
    .answer {
      width: 100%;
    }
  }
  